/* Hide the browser's default checkbox */

.a-option input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  width: 100%;
  height: 100%;
  display: block;
  top: 0;
  left: 0;
  z-index: 2;
}

/* Create a custom checkbox */

.a-option__background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 0;
  background-color: #F3F1EF;
  border-bottom-color: #dbdbdb;
  color: #363636;
  border: 4px solid #B1B3B3;
}

