  border-bottom-color: #7a7a7a;
}

.a-option__background:focus,
.a-option__background.is-focused,
.a-option__background:active,
.a-option__background.isActive {
  border-bottom-color: #753BBD;
}

.a-option__background[disabled] {
  background-color: #F7F6F5;
  background-color: whitesmoke;
  border-bottom-color: whitesmoke;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #7a7a7a;
}

